<template>
  <div class="custom-autocomplete" :class="{ 'is-disabled': disabled, 'has-error': hasError }">
    <!-- Label -->
    <label v-if="label" class="form-label">{{ label }}</label>
    
    <!-- Single Select Mode -->
    <div v-if="!multiple" class="autocomplete-wrapper">
      <div class="input-group">
        <input
          ref="input"
          type="text"
          class="form-control"
          :class="{ 'is-invalid': hasError, 'form-control-sm': density === 'compact', 'form-control-lg': density === 'comfortable' }"
          :placeholder="placeholder"
          :disabled="disabled"
          :value="displayText"
          @input="onInput"
          @focus="onFocus"
          @blur="onBlur"
          @keydown.enter.prevent="handleEnter"
          @keydown.arrow-down.prevent="navigateDown"
          @keydown.arrow-up.prevent="navigateUp"
          @keydown.escape="closeMenu"
        />
        <div class="input-group-append">
          <button
            v-if="clearable && selectedValue"
            class="btn btn-outline-secondary"
            type="button"
            @click="clearSelection"
            tabindex="-1"
          >
            <i class="fa fa-times"></i>
          </button>
          <button
            class="btn btn-outline-secondary dropdown-toggle"
            type="button"
            :class="{ 'active': isMenuOpen }"
            @click="toggleMenu"
            tabindex="-1"
          >
            <i class="fa" :class="loading ? 'fa-spinner fa-spin' : 'fa-chevron-down'"></i>
          </button>
        </div>
      </div>
      
      <!-- Dropdown Menu -->
      <div
        v-if="isMenuOpen"
        ref="menu"
        class="autocomplete-menu"
        :style="{ maxHeight: menuMaxHeight + 'px' }"
        @scroll="onMenuScroll"
      >
        
        <!-- Loading State -->
        <div v-if="loading && !hasItems" class="autocomplete-loading">
          <div class="spinner-border spinner-border-sm text-primary" role="status">
            <span class="sr-only">Loading...</span>
          </div>
          <div class="mt-2 text-muted small">Loading...</div>
        </div>
        
        <!-- No Data State -->
        <div v-else-if="!loading && !hasItems && searchTerm.length >= minSearchLength" class="autocomplete-no-data">
          <i class="fa fa-search text-muted" style="font-size: 2rem;"></i>
          <div class="mt-2 text-muted">{{ noDataText }}</div>
          <div class="text-muted small">Try adjusting your search terms</div>
        </div>
        
        <!-- Grouped Items -->
        <div v-else-if="hasGrouping" class="autocomplete-grouped">
          <div
            v-for="(group, groupIndex) in paginatedGroupedItems"
            :key="group.group || groupIndex"
            class="autocomplete-group"
          >
            <div class="autocomplete-group-header">
              <div v-if="groupFields.length > 0" class="d-flex flex-column">
                <div class="font-weight-bold font-14">{{ group.group }}</div>
                <div class="small text-muted mt-1 d-none">
                  <span v-for="(field, idx) in groupFields" :key="field">
                    <span v-if="idx > 0">  </span>
                    <!-- <span v-if="getGroupFieldValue(group, field)">{{ field }}: {{ getGroupFieldValue(group, field) }}</span> -->
                  </span>
                </div>
              </div>
              <div v-else>{{ group.group }}</div>
            </div>
            <ul class="list-group list-group-flush">
              <li
                v-for="(item, itemIndex) in group.items"
                :key="getItemValue(item)"
                class="list-group-item autocomplete-item"
                :class="{ 
                  'active': getFlatIndex(groupIndex, itemIndex) === highlightedIndex, 
                  'selected': isSelected(item) 
                }"
                @click.prevent="selectItem(item, $event)"
                @mouseenter="highlightedIndex = getFlatIndex(groupIndex, itemIndex)"
                style="cursor: pointer;"
              >
                <div class="d-flex align-items-center">
                  <div v-if="multiple" class="autocomplete-checkbox-wrapper mr-2">
                    <input 
                      type="checkbox" 
                      :checked="isSelected(item)"
                      @click.stop.prevent="selectItem(item, $event)"
                      class="autocomplete-checkbox"
                      tabindex="-1"
                    />
                    <i v-if="isSelected(item)" class="fa fa-check autocomplete-check-icon"></i>
                  </div>
                  <span v-if="item.raw && item.raw.category" class="badge badge-secondary mr-2" style="font-size: 0.75rem;">
                    {{ item.raw.category }}
                  </span>
                  <div class="flex-grow-1">
                    <!-- If itemFields is provided, use formatItemFields instead of getItemText -->
                    <div v-if="itemFields.length > 0" class="font-weight-400" :class="{ 'text-dark': isSelected(item) }" v-html="formatItemFields(item)"></div>
                    <div v-else class="font-weight-400" :class="{ 'text-dark': isSelected(item) }">{{ getItemText(item) }}</div>
                    <div v-if="!itemFields.length && getItemDescription(item)" class="text-muted small mt-1">{{ getItemDescription(item) }}</div>
                  </div>
                  <i v-if="isSelected(item) && !multiple" class="fa fa-check text-success ml-2" style="font-size: 1.1rem;"></i>
                  <!-- <i v-else-if="isSelected(item) && multiple" class="fa fa-check-circle text-success ml-2" style="font-size: 1.1rem;"></i> -->
                </div>
              </li>
            </ul>
          </div>
        </div>
        
        <!-- Flat Items (no grouping) -->
        <ul v-else class="list-group list-group-flush">
          <li
            v-for="(item, index) in paginatedDisplayItems"
            :key="getItemValue(item)"
            class="list-group-item autocomplete-item"
            :class="{ 'active': index === highlightedIndex, 'selected': isSelected(item) }"
            @click.prevent="selectItem(item, $event)"
            @mouseenter="highlightedIndex = index"
            style="cursor: pointer;"
          >
            <div class="d-flex align-items-center">
              <input 
                v-if="multiple" 
                type="checkbox" 
                :checked="isSelected(item)"
                @click.stop.prevent="selectItem(item)"
                class="mr-2"
                tabindex="-1"
              />
              <span v-if="item.raw && item.raw.category" class="badge badge-primary mr-2">
                {{ item.raw.category }}
              </span>
              <div class="flex-grow-1">
                <!-- If itemFields is provided, use formatItemFields instead of getItemText -->
                <div v-if="itemFields.length > 0" class="font-weight-400" v-html="formatItemFields(item)"></div>
                <div v-else class="font-weight-400">{{ getItemText(item) }}</div>
                <div v-if="!itemFields.length && getItemDescription(item)" class="text-muted small mt-1">{{ getItemDescription(item) }}</div>
              </div>
              <i v-if="isSelected(item) && !multiple" class="fa fa-check text-primary ml-2"></i>
            </div>
          </li>
        </ul>
      </div>
    </div>

    <!-- Multi Select Mode -->
    <div v-else class="autocomplete-wrapper">
      <div class="input-group">
        <div class="form-control autocomplete-chips-container" :class="{ 'is-invalid': hasError, 'form-control-sm': density === 'compact', 'form-control-lg': density === 'comfortable' }">
          <div class="d-flex flex-wrap align-items-center">
            <!-- Selected Chips -->
            <template v-for="(selectedItem, index) in visibleChips">
              <span
                v-if="index < maxVisibleChipsCount"
                :key="getItemValue(selectedItem)"
                class="badge badge-primary mr-1 mb-1 pl-2 autocomplete-chip"
                :title="getChipText(selectedItem)"
              >
                <span class="autocomplete-chip-text" v-html="getChipText(selectedItem)"></span>
                <button
                  type="button"
                  class="close ml-1 autocomplete-chip-close"
                  @click.stop="removeItem(getItemValue(selectedItem))"
                  tabindex="-1"
                  :aria-label="'Remove ' + (getChipText(selectedItem).replace(/<[^>]*>/g, ''))"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </span>
            </template>
            <!-- Show "+ n more" if there are more chips than maxVisibleChips -->
            <span
              v-if="remainingChipsCount > 0"
              class="badge badge-secondary mr-1 mb-1 pl-2 autocomplete-chip autocomplete-chip-more"
              :title="`${remainingChipsCount} more selected`"
            >
              <span class="autocomplete-chip-text">+ {{ remainingChipsCount }} more</span>
            </span>
            
            <!-- Input Field -->
            <input
              ref="input"
              type="text"
              class="autocomplete-input-inline"
              :placeholder="selectedItems.length === 0 ? placeholder : ''"
              :disabled="disabled"
              :value="searchTerm"
              @input="onInput"
              @focus="onFocus"
              @blur="onBlur"
              @keydown.enter.prevent="handleEnter"
              @keydown.arrow-down.prevent="navigateDown"
              @keydown.arrow-up.prevent="navigateUp"
              @keydown.escape="closeMenu"
              @keydown.backspace="handleBackspace"
            />
          </div>
        </div>
        <div class="input-group-append">
          <button
            v-if="clearable && (currentValue && (Array.isArray(currentValue) ? currentValue.length > 0 : true))"
            class="btn btn-outline-secondary"
            type="button"
            @click="clearSelection"
            tabindex="-1"
          >
            <i class="fa fa-times"></i>
          </button>
          <button
            class="btn btn-outline-secondary dropdown-toggle"
            type="button"
            :class="{ 'active': isMenuOpen }"
            @click="toggleMenu"
            tabindex="-1"
          >
            <i class="fa" :class="loading ? 'fa-spinner fa-spin' : 'fa-chevron-down'"></i>
          </button>
        </div>
      </div>
      
      <!-- Dropdown Menu (same as single select) -->
      <div
        v-if="isMenuOpen"
        ref="menu"
        class="autocomplete-menu"
        :style="{ maxHeight: menuMaxHeight + 'px' }"
        @scroll="onMenuScroll"
      >
        <!-- Action Buttons -->
        <div v-if="hasActionButtons" class="autocomplete-actions p-2 border-bottom">
          <div class="d-flex flex-wrap gap-1">
            <button
              v-if="actionButtons.selectAll"
              type="button"
              class="btn btn-sm btn-outline-primary"
              @click.stop.prevent="selectAll($event)"
              :disabled="disabled || allSelected"
            >
              <i class="fa fa-check-square mr-1"></i> Select All
            </button>
            <button
              v-if="actionButtons.active"
              type="button"
              class="btn btn-sm btn-outline-success"
              :class="{ 'active': filterStatus === 'active' }"
              @click="filterByStatus('active')"
              :disabled="disabled"
            >
              <i class="fa fa-check-circle mr-1"></i> Active
            </button>
            <button
              v-if="actionButtons.inactive"
              type="button"
              class="btn btn-sm btn-outline-secondary"
              :class="{ 'active': filterStatus === 'inactive' }"
              @click="filterByStatus('inactive')"
              :disabled="disabled"
            >
              <i class="fa fa-times-circle mr-1"></i> Inactive
            </button>
            <button
              v-if="actionButtons.clear"
              type="button"
              class="btn btn-sm btn-outline-danger"
              @click="clearSelection"
              :disabled="disabled || !hasSelectedItems"
            >
              <i class="fa fa-times mr-1"></i> Clear
            </button>
          </div>
        </div>
        
        <div v-if="loading && !hasItems" class="autocomplete-loading">
          <div class="spinner-border spinner-border-sm text-primary" role="status">
            <span class="sr-only">Loading...</span>
          </div>
          <div class="mt-2 text-muted small">Loading...</div>
        </div>
        
        <div v-else-if="!loading && !hasItems && searchTerm.length >= minSearchLength" class="autocomplete-no-data">
          <i class="fa fa-search text-muted" style="font-size: 2rem;"></i>
          <div class="mt-2 text-muted">{{ noDataText }}</div>
          <div class="text-muted small">Try adjusting your search terms</div>
        </div>
        
        <!-- Grouped Items -->
        <div v-else-if="hasGrouping" class="autocomplete-grouped">
          <div
            v-for="(group, groupIndex) in paginatedGroupedItems"
            :key="group.group || groupIndex"
            class="autocomplete-group"
          >
            <div class="autocomplete-group-header">
              <div v-if="groupFields.length > 0" class="d-flex flex-column">
                <div class="font-weight-bold font-14">{{ group.group }}</div>
                <div class="small text-muted mt-1 d-none">
                  <span v-for="(field, idx) in groupFields" :key="field">
                    <span v-if="idx > 0">  </span>
                    <span v-if="getGroupFieldValue(group, field)">{{ field }}: {{ getGroupFieldValue(group, field) }}</span>
                  </span>
                </div>
              </div>
              <div v-else>{{ group.group }}</div>
            </div>
            <ul class="list-group list-group-flush">
              <li
                v-for="(item, itemIndex) in group.items"
                :key="getItemValue(item)"
                class="list-group-item autocomplete-item"
                :class="{ 
                  'active': getFlatIndex(groupIndex, itemIndex) === highlightedIndex, 
                  'selected': isSelected(item) 
                }"
                @click.prevent="selectItem(item, $event)"
                @mouseenter="highlightedIndex = getFlatIndex(groupIndex, itemIndex)"
                style="cursor: pointer;"
              >
                <div class="d-flex align-items-center">
                  <div v-if="multiple" class="autocomplete-checkbox-wrapper mr-2">
                    <input 
                      type="checkbox" 
                      :checked="isSelected(item)"
                      @click.stop.prevent="selectItem(item, $event)"
                      class="autocomplete-checkbox"
                      tabindex="-1"
                    />
                    <i v-if="isSelected(item)" class="fa fa-check autocomplete-check-icon"></i>
                  </div>
                  <span v-if="item.raw && item.raw.category" class="badge badge-secondary mr-2" style="font-size: 0.75rem;">
                    {{ item.raw.category }}
                  </span>
                  <div class="flex-grow-1">
                    <!-- If itemFields is provided, use formatItemFields instead of getItemText -->
                    <div v-if="itemFields.length > 0" class="font-weight-400" :class="{ 'text-dark': isSelected(item) }" v-html="formatItemFields(item)"></div>
                    <div v-else class="font-weight-400" :class="{ 'text-dark': isSelected(item) }">{{ getItemText(item) }}</div>
                    <div v-if="!itemFields.length && getItemDescription(item)" class="text-muted small mt-1">{{ getItemDescription(item) }}</div>
                  </div>
                  <i v-if="isSelected(item) && !multiple" class="fa fa-check text-success ml-2" style="font-size: 1.1rem;"></i>
                  <!-- <i v-else-if="isSelected(item) && multiple" class="fa fa-check-circle text-success ml-2" style="font-size: 1.1rem;"></i> -->
                </div>
              </li>
            </ul>
          </div>
        </div>
        
        <!-- Flat Items (no grouping) -->
        <ul v-else class="list-group list-group-flush">
          <li
            v-for="(item, index) in paginatedDisplayItems"
            :key="getItemValue(item)"
            class="list-group-item autocomplete-item"
            :class="{ 'active': index === highlightedIndex, 'selected': isSelected(item) }"
            @click.prevent="selectItem(item, $event)"
            @mouseenter="highlightedIndex = index"
            style="cursor: pointer;"
          >
            <div class="d-flex align-items-center">
              <input 
                v-if="multiple" 
                type="checkbox" 
                :checked="isSelected(item)"
                @click.stop.prevent="selectItem(item)"
                class="mr-2"
                tabindex="-1"
              />
              <span v-if="item.raw && item.raw.category" class="badge badge-primary mr-2">
                {{ item.raw.category }}
              </span>
              <div class="flex-grow-1">
                <!-- If itemFields is provided, use formatItemFields instead of getItemText -->
                <div v-if="itemFields.length > 0" class="font-weight-400" v-html="formatItemFields(item)"></div>
                <div v-else class="font-weight-400">{{ getItemText(item) }}</div>
                <div v-if="!itemFields.length && getItemDescription(item)" class="text-muted small mt-1">{{ getItemDescription(item) }}</div>
              </div>
              <i v-if="isSelected(item) && !multiple" class="fa fa-check text-primary ml-2"></i>
            </div>
          </li>
        </ul>
      </div>
    </div>

    <!-- Error Messages -->
    <div v-if="!hideDetails && hasError" class="invalid-feedback d-block">
      <div v-for="(error, index) in errorMessagesArray" :key="index">{{ error }}</div>
    </div>
    </div>
</template>

<script>
export default {
  name: 'Autocomplete',
  // Vue 2 v-model uses value/input by default
  props: {
    value: {
      type: [String, Number, Array],
      default: null
    },
    // Also support modelValue for Vue 3 compatibility
    modelValue: {
      type: [String, Number, Array],
      default: null
    },
    items: {
      type: Array,
      default: () => []
    },
    // All items (unfiltered) - used for finding selected items
    allItems: {
      type: Array,
      default: null
    },
    loading: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: 'Search and select...'
    },
    label: {
      type: String,
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: true
    },
    density: {
      type: String,
      default: 'default',
      validator: (value) => ['compact', 'comfortable', 'default'].includes(value)
    },
    variant: {
      type: String,
      default: 'outlined',
      validator: (value) => ['outlined', 'filled', 'underlined', 'solo', 'solo-inverted', 'solo-filled'].includes(value)
    },
    multiple: {
      type: Boolean,
      default: false
    },
    itemTitle: {
      type: String,
      default: 'title'
    },
    itemValue: {
      type: String,
      default: 'value'
    },
    noDataText: {
      type: String,
      default: 'No data available'
    },
    hideDetails: {
      type: Boolean,
      default: false
    },
    errorMessages: {
      type: [String, Array],
      default: null
    },
    rules: {
      type: Array,
      default: () => []
    },
    menuProps: {
      type: Object,
      default: () => ({ maxHeight: 300 })
    },
    minSearchLength: {
      type: Number,
      default: 0
    },
    // Maximum number of chips to display before showing "+ n more"
    maxVisibleChips: {
      type: Number,
      default: null // null means show all
    },
    // Action buttons configuration
    actionButtons: {
      type: Object,
      default: () => ({
        selectAll: false,
        active: false,
        inactive: false,
        clear: false
      })
    },
    // Item display fields - array of field names to display
    itemFields: {
      type: Array,
      default: () => [] // e.g., ['name', 'number', 'status']
    },
    // Group display fields - array of field names to display in group header
    groupFields: {
      type: Array,
      default: () => [] // e.g., ['accountName', 'accountNumber']
    },
    // Performance: Maximum items to render at once (virtualization)
    maxRenderItems: {
      type: Number,
      default: 100 // Render first 100 items, load more on scroll
    },
    // Performance: Enable lazy loading
    lazyLoad: {
      type: Boolean,
      default: false
    },
    // Status field configuration for dynamic color formatting
    statusFieldConfig: {
      type: Object,
      default: () => ({
        fieldName: 'status', // Field name to check for status
        activeValues: ['active', 'Active', 'true', '1'], // Values considered as active
        inactiveValues: ['inactive', 'Inactive', 'false', '0'], // Values considered as inactive
        activeColor: '#28a745', // Green color for active
        inactiveColor: '#dc3545' // Red color for inactive
      })
    }
  },
  data() {
    return {
      isMenuOpen: false,
      searchTerm: '',
      highlightedIndex: -1,
      renderedItemCount: this.maxRenderItems,
      filterStatus: null // 'active', 'inactive', or null for all
    }
  },
  computed: {
    // Get the current value (Vue 2 uses 'value', Vue 3 uses 'modelValue')
    currentValue() {
      return this.value !== null && this.value !== undefined ? this.value : this.modelValue
    },
    // Selected value for single select mode
    selectedValue() {
      if (this.multiple) return null
      return this.currentValue
    },
    displayItems() {
      return this.items.map(item => ({
        ...item,
        title: this.getItemText(item),
        value: this.getItemValue(item)
      }))
    },
    groupedItems() {
      // Check if items are already grouped (array of groups) or flat with group property
      if (this.items.length > 0 && Array.isArray(this.items[0].items)) {
        // Items are already in grouped format: [{ group: 'Group1', items: [...] }, ...]
        // Preserve all group properties for groupFields display
        return this.items.map(group => ({
          ...group,
          raw: group, // Preserve original group data for groupFields
          group: group.group || group.groupName || ''
        }))
      }
      
      // Group flat items by group property
      const groups = {}
      const groupMetadata = {} // Store metadata for each group (for groupFields display)
      
      this.displayItems.forEach(item => {
        const groupName = this.getItemGroup(item) || 'Other'
        if (!groups[groupName]) {
          groups[groupName] = []
          // Store first item's group metadata for display
          if (item.raw) {
            groupMetadata[groupName] = { ...item.raw }
          } else {
            groupMetadata[groupName] = { ...item }
          }
        }
        groups[groupName].push(item)
      })
      
      // Convert to array format with metadata
      return Object.keys(groups).map(groupName => ({
        group: groupName,
        items: groups[groupName],
        raw: groupMetadata[groupName] // Preserve group metadata for groupFields display
      }))
    },
    hasGrouping() {
      if (this.items.length === 0) return false
      // Check if items are in grouped format
      if (Array.isArray(this.items[0].items)) return true
      // Check if items have group property or groupFields is defined
      if (this.groupFields && this.groupFields.length > 0) {
        // If groupFields is defined, we should group by the first field
        return true
      }
      // Check if items have explicit group property
      return this.displayItems.some(item => this.getItemGroup(item))
    },
    flatDisplayItems() {
      // Flatten grouped items for easier iteration
      if (this.hasGrouping) {
        return this.groupedItems.flatMap(group => group.items)
      }
      return this.displayItems
    },
    hasItems() {
      return this.displayItems.length > 0
    },
    menuMaxHeight() {
      return (this.menuProps && this.menuProps.maxHeight) || 300
    },
    selectedItems() {
      if (!this.multiple) return []
      
      // Get selected values from currentValue (supports both value and modelValue props)
      const currentVal = this.currentValue
      const selectedValues = Array.isArray(currentVal) ? currentVal : (currentVal ? [currentVal] : [])
      
      if (selectedValues.length === 0) {
        return []
      }
      
      // Look through ALL items (unfiltered), not just displayItems (which might be filtered)
      let itemsToSearch = (this.allItems && this.allItems.length > 0) ? this.allItems : this.items
      
      // Flatten grouped items if needed
      if (itemsToSearch.length > 0 && Array.isArray(itemsToSearch[0].items)) {
        // Items are in grouped format - flatten them
        itemsToSearch = itemsToSearch.flatMap(group => group.items || [])
      }
      
      const filtered = itemsToSearch.filter(item => {
        const itemValue = this.getItemValue(item)
        return selectedValues.some(sv => String(sv) === String(itemValue))
      })
      
      return filtered.map(item => ({
        ...item,
        title: this.getItemText(item),
        value: this.getItemValue(item)
      }))
    },
    displayText() {
      if (this.multiple) return ''
      const currentVal = this.currentValue
      if (!currentVal) return this.searchTerm
      const selected = this.displayItems.find(item => 
        String(this.getItemValue(item)) === String(currentVal)
      )
      return selected ? this.getItemText(selected) : this.searchTerm
    },
    hasError() {
      return (this.errorMessages && this.errorMessages.length > 0) || 
             (this.rules && this.rules.length > 0 && this.validateRules().length > 0)
    },
    errorMessagesArray() {
      if (this.errorMessages) {
        return Array.isArray(this.errorMessages) ? this.errorMessages : [this.errorMessages]
      }
      return this.validateRules()
    },
    // Chips display logic
    visibleChips() {
      return this.selectedItems
    },
    maxVisibleChipsCount() {
      return this.maxVisibleChips !== null && this.maxVisibleChips !== undefined 
        ? this.maxVisibleChips 
        : this.selectedItems.length
    },
    remainingChipsCount() {
      if (this.maxVisibleChips === null || this.maxVisibleChips === undefined) return 0
      return Math.max(0, this.selectedItems.length - this.maxVisibleChips)
    },
    // Action buttons
    hasActionButtons() {
      return this.actionButtons && (
        this.actionButtons.selectAll || 
        this.actionButtons.active || 
        this.actionButtons.inactive || 
        this.actionButtons.clear
      )
    },
    allSelected() {
      if (!this.multiple) return false
      // Check if all filtered items are selected
      const filteredItems = this.filteredDisplayItems
      const selectedValues = Array.isArray(this.currentValue) ? this.currentValue : []
      return filteredItems.length > 0 && filteredItems.every(item => {
        const itemValue = this.getItemValue(item)
        return selectedValues.some(sv => String(sv) === String(itemValue))
      })
    },
    hasSelectedItems() {
      if (!this.multiple) return false
      const selectedValues = Array.isArray(this.currentValue) ? this.currentValue : []
      return selectedValues.length > 0
    },
    // Performance: Filter and paginate items
    filteredDisplayItems() {
      let items = this.displayItems
      
      // Apply status filter if active
      if (this.filterStatus) {
        items = items.filter(item => {
          // Check status field (case-insensitive)
          const status = this.getItemFieldValue(item, 'status') || this.getItemFieldValue(item, 'Status') || this.getItemFieldValue(item, 'isActive')
          const statusStr = String(status).toLowerCase().trim()
          
          if (this.filterStatus === 'active') {
            return statusStr === 'active' || statusStr === 'true' || statusStr === '1'
          } else if (this.filterStatus === 'inactive') {
            return statusStr === 'inactive' || statusStr === 'false' || statusStr === '0'
          }
          return true
        })
      }
      
      return items
    },
    // Performance: Limit rendered items
    paginatedDisplayItems() {
      if (this.lazyLoad && this.filteredDisplayItems.length > this.renderedItemCount) {
        return this.filteredDisplayItems.slice(0, this.renderedItemCount)
      }
      return this.filteredDisplayItems
    },
    // Update grouped items to use paginated/filtered items
    paginatedGroupedItems() {
      if (!this.hasGrouping) return []
      
      let groups = this.groupedItems
      
      // Apply status filter if active
      if (this.filterStatus) {
        groups = groups.map(group => ({
          ...group,
          items: group.items.filter(item => {
            // Check status field (case-insensitive)
            const status = this.getItemFieldValue(item, 'status') || this.getItemFieldValue(item, 'Status') || this.getItemFieldValue(item, 'isActive')
            const statusStr = String(status).toLowerCase().trim()
            
            if (this.filterStatus === 'active') {
              return statusStr === 'active' || statusStr === 'true' || statusStr === '1'
            } else if (this.filterStatus === 'inactive') {
              return statusStr === 'inactive' || statusStr === 'false' || statusStr === '0'
            }
            return true
          })
        })).filter(group => group.items.length > 0)
      }
      
      // Apply pagination if lazy load is enabled
      if (this.lazyLoad) {
        let itemCount = 0
        const result = []
        for (const group of groups) {
          if (itemCount >= this.renderedItemCount) break
          const remaining = this.renderedItemCount - itemCount
          result.push({
            ...group,
            items: group.items.slice(0, remaining)
          })
          itemCount += group.items.length
        }
        return result
      }
      
      return groups
    }
  },
  watch: {
    items() {
      // Reset highlight when items change
      this.highlightedIndex = -1
    },
    hasGrouping() {
      // Reset highlight when grouping changes
      this.highlightedIndex = -1
    },
    modelValue: {
      handler(newVal, oldVal) {
        // Reset search term when value changes externally
        if (this.multiple) {
          // For multiple, keep search term
        } else {
          // For single select, clear search term if value is cleared
          if (!newVal && this.searchTerm) {
            this.searchTerm = ''
          }
        }
      },
      immediate: true,
      deep: true
    },
    value: {
      handler(newVal, oldVal) {
        // Reset search term when value changes externally
        if (this.multiple) {
          // For multiple, keep search term
        } else {
          // For single select, clear search term if value is cleared
          if (!newVal && this.searchTerm) {
            this.searchTerm = ''
          }
        }
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    // Close menu when clicking outside
    document.addEventListener('click', this.handleClickOutside)
  },
  beforeDestroy() {
    document.removeEventListener('click', this.handleClickOutside)
  },
  methods: {
    getItemText(item) {
      if (item.raw) {
        return item.raw.text || item.raw.title || item.raw[this.itemTitle] || ''
      }
      return item.text || item.title || item[this.itemTitle] || ''
    },
    getItemValue(item) {
      let value
      if (item.raw) {
        value = item.raw.value !== undefined && item.raw.value !== null 
          ? item.raw.value 
          : (item.raw[this.itemValue] !== undefined && item.raw[this.itemValue] !== null ? item.raw[this.itemValue] : null)
      } else {
        value = item.value !== undefined && item.value !== null 
          ? item.value 
          : (item[this.itemValue] !== undefined && item[this.itemValue] !== null ? item[this.itemValue] : null)
      }
      
      // Ensure we have a valid value - use index as fallback if needed
      if (value === null || value === undefined) {
        // Try to find the item's index in the items array
        const itemIndex = this.items.findIndex(i => i === item || (item.raw && i === item.raw))
        if (itemIndex > -1) {
          value = `item-${itemIndex}`
        } else {
          console.warn('Autocomplete: Item has no value and could not determine index', item)
          value = `item-${Date.now()}-${Math.random()}`
        }
      }
      
      return value
    },
    getItemDescription(item) {
      if (item.raw) {
        return item.raw.description || item.raw.subtitle || ''
      }
      return item.description || item.subtitle || ''
    },
    getItemGroup(item) {
      if (item.raw) {
        // Priority: groupFields > group/groupName
        // If groupFields is defined, use the first field as grouping field (highest priority)
        if (this.groupFields && this.groupFields.length > 0) {
          const groupValue = item.raw[this.groupFields[0]]
          if (groupValue !== undefined && groupValue !== null && groupValue !== '') {
            return String(groupValue)
          }
        }
        // Fallback to explicit group/groupName
        if (item.raw.group || item.raw.groupName) {
          return item.raw.group || item.raw.groupName
        }
        return ''
      }
      // Priority: groupFields > group/groupName
      // If groupFields is defined, use the first field as grouping field (highest priority)
      if (this.groupFields && this.groupFields.length > 0) {
        const groupValue = item[this.groupFields[0]]
        if (groupValue !== undefined && groupValue !== null && groupValue !== '') {
          return String(groupValue)
        }
      }
      // Fallback to explicit group/groupName
      if (item.group || item.groupName) {
        return item.group || item.groupName
      }
      return ''
    },
    getItemFieldValue(item, field) {
      if (item.raw) {
        return item.raw[field] || ''
      }
      return item[field] || ''
    },
    getGroupFieldValue(group, field) {
      if (group.raw) {
        return group.raw[field] || ''
      }
      return group[field] || ''
    },
    isSelected(item) {
      const value = this.getItemValue(item)
      const currentVal = this.currentValue
      if (this.multiple) {
        const selectedValues = Array.isArray(currentVal) ? currentVal : (currentVal ? [currentVal] : [])
        // Compare values with type coercion to handle string/number mismatches
        return selectedValues.some(sv => String(sv) === String(value))
      }
      return currentVal !== null && currentVal !== undefined && String(currentVal) === String(value)
    },
    onInput(event) {
      this.searchTerm = event.target.value
      this.isMenuOpen = true
      this.$emit('update:search', this.searchTerm)
      this.$emit('search', this.searchTerm)
    },
    onFocus() {
      this.isMenuOpen = true
      this.$emit('focus')
    },
    onBlur() {
      // Delay to allow click events to fire
      setTimeout(() => {
        if (!this.multiple) {
          // Restore selected value text if no selection made
          if (this.selectedValue) {
            const selected = this.displayItems.find(item => 
              this.getItemValue(item) === this.selectedValue
            )
            if (selected && this.searchTerm !== this.getItemText(selected)) {
              this.searchTerm = ''
            }
          }
        }
        this.$emit('blur')
      }, 200)
    },
    toggleMenu() {
      if (this.disabled) return
      this.isMenuOpen = !this.isMenuOpen
      if (this.isMenuOpen) {
        this.$nextTick(() => {
          this.$refs.input?.focus()
        })
      }
    },
    closeMenu() {
      this.isMenuOpen = false
      this.highlightedIndex = -1
    },
    selectItem(item, event) {
      // Prevent event from bubbling and default behavior
      if (event) {
        event.preventDefault()
        event.stopPropagation()
        event.stopImmediatePropagation()
      }
      
      // Debug logging
      console.log('selectItem called for single item:', item)
      console.log('Item value:', this.getItemValue(item))
      
      const value = this.getItemValue(item)
      const currentVal = this.currentValue
      
      if (this.multiple) {
        // Ensure we start with an array
        const currentValues = Array.isArray(currentVal) ? [...currentVal] : (currentVal ? [currentVal] : [])
        const selectedValues = [...currentValues]
        
        // Find index with type coercion
        const index = selectedValues.findIndex(sv => String(sv) === String(value))
        
        console.log('Current selected values:', selectedValues)
        console.log('Item value to toggle:', value)
        console.log('Index found:', index)
        
        if (index > -1) {
          // Remove if already selected
          selectedValues.splice(index, 1)
          console.log('Removed item. New selection:', selectedValues)
        } else {
          // Add if not selected - ONLY this single item
          selectedValues.push(value)
          console.log('Added single item. New selection:', selectedValues)
        }
        
        // Emit both events for Vue 2 and Vue 3 compatibility
        this.$emit('input', selectedValues)
        this.$emit('update:modelValue', selectedValues)
        this.$emit('change', selectedValues)
      } else {
        this.searchTerm = ''
        this.isMenuOpen = false
        // Emit both events for Vue 2 and Vue 3 compatibility
        this.$emit('input', value)
        this.$emit('update:modelValue', value)
        this.$emit('change', value)
      }
    },
    removeItem(value) {
      if (this.multiple) {
        const currentVal = this.currentValue
        const selectedValues = Array.isArray(currentVal) ? [...currentVal] : []
        const index = selectedValues.findIndex(sv => String(sv) === String(value))
        if (index > -1) {
          selectedValues.splice(index, 1)
          // Emit both events for Vue 2 and Vue 3 compatibility
          this.$emit('input', selectedValues)
          this.$emit('update:modelValue', selectedValues)
          this.$emit('change', selectedValues)
        }
      }
    },
    clearSelection() {
      if (this.multiple) {
        // Emit both events for Vue 2 and Vue 3 compatibility
        this.$emit('input', [])
        this.$emit('update:modelValue', [])
        this.$emit('change', [])
      } else {
        this.searchTerm = ''
        // Emit both events for Vue 2 and Vue 3 compatibility
        this.$emit('input', null)
        this.$emit('update:modelValue', null)
        this.$emit('change', null)
      }
      this.isMenuOpen = false
    },
    getFlatIndex(groupIndex, itemIndex) {
      // Calculate flat index from group and item indices
      let flatIndex = 0
      for (let i = 0; i < groupIndex; i++) {
        flatIndex += this.groupedItems[i].items.length
      }
      return flatIndex + itemIndex
    },
    getItemFromFlatIndex(flatIndex) {
      // Get item from flat index when items are grouped
      if (!this.hasGrouping) {
        return this.displayItems[flatIndex]
      }
      
      let currentIndex = 0
      for (const group of this.groupedItems) {
        if (flatIndex < currentIndex + group.items.length) {
          return group.items[flatIndex - currentIndex]
        }
        currentIndex += group.items.length
      }
      return null
    },
    handleEnter() {
      if (this.isMenuOpen && this.highlightedIndex >= 0) {
        const item = this.hasGrouping 
          ? this.getItemFromFlatIndex(this.highlightedIndex)
          : this.displayItems[this.highlightedIndex]
        
        if (item) {
          this.selectItem(item, null)
        }
      } else if (this.isMenuOpen && this.flatDisplayItems.length === 1) {
        this.selectItem(this.flatDisplayItems[0], null)
      }
    },
    navigateDown() {
      if (!this.isMenuOpen) {
        this.isMenuOpen = true
        return
      }
      const maxIndex = this.flatDisplayItems.length - 1
      if (this.highlightedIndex < maxIndex) {
        this.highlightedIndex++
        this.scrollToHighlighted()
      }
    },
    navigateUp() {
      if (this.highlightedIndex > 0) {
        this.highlightedIndex--
        this.scrollToHighlighted()
      }
    },
    scrollToHighlighted() {
      this.$nextTick(() => {
        if (this.$refs.menu) {
          const items = this.$refs.menu.querySelectorAll('.autocomplete-item')
          if (items[this.highlightedIndex]) {
            items[this.highlightedIndex].scrollIntoView({ block: 'nearest', behavior: 'smooth' })
          }
        }
      })
    },
    handleBackspace() {
      if (this.multiple && this.searchTerm === '') {
        const currentVal = this.currentValue
        const selectedValues = Array.isArray(currentVal) ? currentVal : []
        if (selectedValues.length > 0) {
          this.removeItem(selectedValues[selectedValues.length - 1])
        }
      }
    },
    handleClickOutside(event) {
      if (this.$el && !this.$el.contains(event.target)) {
        this.closeMenu()
      }
    },
    validateRules() {
      if (!this.rules || this.rules.length === 0) return []
      const errors = []
      const value = this.currentValue
      
      this.rules.forEach(rule => {
        if (typeof rule === 'function') {
          const result = rule(value)
          if (result !== true && result !== undefined) {
            errors.push(result)
          }
        }
      })
      
      return errors
    },
    // Action button methods
    selectAll(event) {
      // Prevent event from bubbling
      if (event) {
        event.preventDefault()
        event.stopPropagation()
        event.stopImmediatePropagation()
      }
      
      console.log('selectAll called - this should only be called from the Select All button')
      
      if (!this.multiple) return
      // Select all items from the currently filtered/displayed items
      const allItems = this.filteredDisplayItems
      const allValues = allItems.map(item => this.getItemValue(item))
      // Merge with existing selections to avoid deselecting items not in current filter
      const currentValues = Array.isArray(this.currentValue) ? this.currentValue : []
      const mergedValues = [...new Set([...currentValues, ...allValues])]
      
      console.log('Select All - all values:', mergedValues)
      
      this.$emit('input', mergedValues)
      this.$emit('update:modelValue', mergedValues)
      this.$emit('change', mergedValues)
    },
    filterByStatus(status) {
      if (this.filterStatus === status) {
        // Toggle off if clicking the same filter
        this.filterStatus = null
      } else {
        this.filterStatus = status
      }
      // Reset pagination when filter changes
      this.renderedItemCount = this.maxRenderItems
      // Reset highlight when filter changes
      this.highlightedIndex = -1
    },
    formatItemFields(item) {
      if (!this.itemFields || this.itemFields.length === 0) return ''
      
      const formattedValues = this.itemFields.map((field, index) => {
        const value = this.getItemFieldValue(item, field)
        if (!value && value !== 0) return ''
        
        // Check if this field is the status field and apply color formatting
        if (field.toLowerCase() === this.statusFieldConfig.fieldName.toLowerCase()) {
          const statusStr = String(value).toLowerCase().trim()
          const isActive = this.statusFieldConfig.activeValues.some(av => 
            String(av).toLowerCase() === statusStr
          )
          const isInactive = this.statusFieldConfig.inactiveValues.some(iv => 
            String(iv).toLowerCase() === statusStr
          )
          
          let color = ''
          if (isActive) {
            color = this.statusFieldConfig.activeColor
          } else if (isInactive) {
            color = this.statusFieldConfig.inactiveColor
          }
          
          if (color) {
            return `<span style="color: ${color};">${value}</span>`
          }
        }
        
        return String(value)
      }).filter(v => v !== '')
      
      if (formattedValues.length === 0) return ''
      
      // Format: {field1} - {field2} ({field3})
      // Example: customerName - customerNumber (Status)
      if (formattedValues.length === 1) {
        return formattedValues[0]
      } else if (formattedValues.length === 2) {
        // field1 - field2
        return `${formattedValues[0]} - ${formattedValues[1]}`
      } else if (formattedValues.length >= 3) {
        // field1 - field2 (field3, field4, ...)
        const firstTwo = formattedValues.slice(0, 2).filter(v => v).join(' - ')
        const rest = formattedValues.slice(2).filter(v => v).join(', ')
        if (rest) {
          return `${firstTwo} (${rest})`
        }
        return firstTwo
      }
      return ''
    },
    getChipText(item) {
      // If itemFields is provided, use formatItemFields, otherwise use getItemText
      if (this.itemFields && this.itemFields.length > 0) {
        return this.formatItemFields(item) || this.getItemText(item)
      }
      return this.getItemText(item)
    },
    onMenuScroll(event) {
      if (!this.lazyLoad) return
      const target = event.target
      const scrollBottom = target.scrollHeight - target.scrollTop - target.clientHeight
      
      // Load more items when scrolled near bottom (within 50px)
      if (scrollBottom < 50 && this.renderedItemCount < this.filteredDisplayItems.length) {
        this.renderedItemCount = Math.min(
          this.renderedItemCount + this.maxRenderItems,
          this.filteredDisplayItems.length
        )
      }
    }
  }
}
</script>





</style>

